*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body{
  height: 100vh;
  overflow: hidden;

}
.tasks{
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%,-50%);
  padding: 20px;
  width: 700px;
  min-height: 400px;
  background-color: grey;
}
h1{
  text-align: center;
  color: #fff;
}
.task{
  display: flex;
  justify-content: space-between;
  align-items: center;
  border: 1px blue solid;
  border-radius: 10px;
  margin: 5px;
  padding: 5px;
}
span{
  color: #fff;
  font-size: 30px;
}

.task button{
  margin: 5px;
  width: 100px;
  height: 35px;
  border-radius: 10px;
  color: #fff;
  border: none;
  cursor: pointer;

}
.task .done{
  background-color: green;
}
.task .delete{
  background-color: red;
}
.add input{
  width: 655px;
  height: 50px;
  border-radius: 10px;
  font-size: 20px;
  border: none;
}